@import "../../css-config/mixins.scss";

.main-header {
    width: 100%;
    height: 8vh;
    padding: 0 20px;
    background-color: #212121;
    color: aqua;
    border-bottom: 1px solid rgba(0,0,0,0.1);
    z-index: 20;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .header-content {
        .left{
            display: flex;
            flex-direction: row;
            list-style: none;
            gap: 30px;
        }
        .center{
            padding: 2px;
        }
        .right{
            border: 2px solid yellow;
        }
    }
}
